<template lang='pug'>
  aside.sidebar
    template(v-for='offer in handlers.sidebar.offers')
      .sidebar-offer-dop
        span.sidebar-offer-dop__title {{ offer.title }}
        p.sidebar-offer-dop__p {{ offer.p1 }}
        img.sidebar-offer-dop__img(:src='offer.img')
        p.sidebar-offer-dop__p {{ offer.p2 }}
        .btn-blue(@click='showPopup({ name: "commonPopup" })') 

    .sidebar__cont.about-veka
      span.about-veka__title  :
      .about-veka__content
        img.about-veka__img(src='@/assets/images/sidebar/veka.png')
        p.about-veka__p &nbsp;  <b>VEKA</b>   &nbsp;<b> &nbsp;</b>       &nbsp; . &nbsp;2004   VEKA     &nbsp;,  <b></b>    <b>DIN ISO 9001:2000.</b> &nbsp;2006  VEKA Rus   &nbsp;  &nbsp;,     &nbsp;   <b> RAL</b>
        .btn-blue(@click='showPopup({ name: "commonPopup" })') 

    .sidebar__cont.advantages
      span.advantages__title :
      ul.advantages-list
        li.advantages-list__item
          span.advantages-list__title    
          p.advantages-list__p  70% .             ,   .
        li.advantages-list__item
          span.advantages-list__title   MACO
          p.advantages-list__p            .
        li.advantages-list__item
          span.advantages-list__title    

    .sidebar__cont.sidebar-offer
      span.sidebar-offer__title   
      img(src='@/assets/images/sidebar/akciya-na-okno.png', alt='   ""')
      p.sidebar-offer__p <b></b> , <b></b>  &nbsp; <b></b> .         <b></b>   ,  &nbsp;  
      .btn-blue(@click='showPopup({ name: "commonPopup" })') 

    .sidebar-video
      img.sidebar-video__img(src='@/assets/images/sidebar/video-thumbnail-01.jpg')

    .sidebar-articles
      .sidebar-articles__heading
        span.sidebar-articles__title  
        p.sidebar-articles__p  &nbsp;  &nbsp;.  .    &nbsp;&nbsp;.
      ul.sidebar-articles-list
        li.sidebar-articles-list__item
          a.sidebar-articles-list__link(href='#')    &nbsp; 
        li.sidebar-articles-list__item
          a.sidebar-articles-list__link(href='#')    &nbsp; . .
        li.sidebar-articles-list__item
          a.sidebar-articles-list__link(href='#')    &nbsp; .
        li.sidebar-articles-list__item
          a.sidebar-articles-list__link(href='#')    &nbsp; .
        li.sidebar-articles-list__item
          a.sidebar-articles-list__link(href='#')    &nbsp; .

    .sidebar-video(v-if='handlers.sidebar.video')
      img.sidebar-video__img(src='@/assets/images/sidebar/video-thumbnail-02.jpg')
</template>

<script>
  import Mixins from '../../assets/scripts/mixins'

  export default {
    mixins: [Mixins],
    props: {
      handlers: Object
    }
  }
</script>

<style lang='stylus'>
  .sidebar
    width: 100%
    max-width: 320px
    flex: 320px 0 0
    margin: 0 0 0 auto

    &__cont
      padding: 0 20px
      border-top: 5px solid #DEDEDE
      background: linear-gradient(0deg, #ECECEC, #F9F9F9 46%, #FFFFFF)
      box-sizing: border-box

  .about-veka
    padding-top: 10px
    padding-bottom: 10px

    &__title
      display: inline-block
      color: #009DEA
      font-size: 20px

    &__content
      margin: 10px 0 0

    &__img
      width: 105px
      height: 90px
      display: block
      margin: 0 10px 10px 0
      top: 14px
      position: relative
      float: left

    &__p
      padding: 0 0 10px
      color: #000000
      font-size 13px
      border-bottom: 1.3px dotted #898989

  .advantages
    margin: 10px 0 0
    padding: 0 20px 20px
    background: linear-gradient(180deg, #ECECEC, #F9F9F9 80%, #FFFFFF)

    &__title
      width: 100%
      display: inline-block
      padding: 20px 0
      font-size: 20px
      font-weight: 600
      border-bottom: 1.3px dotted #898989

  .advantages-list
    margin: 0
    padding: 0

    &__item
      margin: 20px 0 0
      list-style: none

    &__title
      display: inline
      color: #009DEA
      font-size: 13px
      text-decoration: underline
      line-height: 18px

      &:before
        width: 10px
        height: 10px
        margin: 0 6px 0 0
        flex: 10px 0 0
        content: ''
        display: inline-block
        background: #009DEA
        border-radius: 100%

    &__p
      margin: 10px 0 0
      font-size: 13px
      line-height: 16px

  .sidebar-offer
    margin: 10px 0 0
    padding: 20px 32px 10px

    &__title
      font-size: 19px
      font-weight: 400

    &__p
      margin: 20px 0 0
      padding: 0 20px 20px 0
      font-size: 13px
      line-height: 16px
      border-bottom: 1.3px dotted #898989

  .sidebar-video
    margin: 10px 0 0
    cursor: pointer

    &__img
      width: 100%

  .sidebar-articles
    margin: 20px 0 0

    &__heading
      padding: 20px
      border-top: 1.3px dotted #898989
      border-bottom: 1.3px dotted #898989
      box-sizing: border-box

    &__title
      display: flex
      margin: 0 0 14px
      color: #00A0E3
      font-size: 18px
      font-weight: 600
      text-transform: uppercase

      &:after
        width: 30px
        height: 18px
        content: ''
        display: inline-block
        margin: 0 0 0 10px
        background-image: url('data:image/svg+xml;base64,')
        background-position: center center
        background-repeat: no-repeat
        background-size: 100%

    &__p
      margin: 0
      color: #000000
      font-size: 14px

  .sidebar-articles-list
    margin: 0
    padding: 20px

    &__item
      margin: 0 0 8px
      list-style: none
      list-style-image: url('data:image/svg+xml;base64,')

      &:last-of-type
        margin: 0

    &__link
      display: flex
      align-items: baseline
      color: #00A0E3
      font-size: 14px
      text-decoration: underline
      transition: .2s

      &:hover
        color: lighten(#00A0E3, 20%)

  .sidebar-offer-dop
    margin: 0 0 10px
    padding: 20px 20px 10px
    box-sizing: border-box
    border-top: 5px solid #DEDEDE
    background: linear-gradient(0deg, #ECECEC, #F9F9F9 46%, #FFFFFF)

    &__title
      display: inline-block
      margin: 0 0 10px
      color: #898989
      font-size: 19px
      font-weight: 600

    &__p
      margin: 0
      color: #898989
      font-size: 13px

      &:last-of-type
        padding: 0 0 20px
        border-bottom: 1.3px dotted #898989

    &__img
      margin: 20px 0


  @media (max-width: 991px)
    .sidebar
      display: none
</style>



