<template lang='pug'>
  section.balkony-heading
    img.balkony-heading__img-main(
      src='@/assets/images/balkony-page/balkony-heading/banner.jpg'
      alt=' 50%.      '
      @click='showPopup({ name: "commonPopup" })'
    )
    .balkony-heading__cont
      h1.balkony-heading__title   &nbsp; &nbsp;
      h2.balkony-heading__subtitle  &laquo;&raquo; 
      p.balkony-heading__p-main    &nbsp;<b>,</b> ,   &nbsp;  <b> </b>
      img.balkony-heading__img(src='@/assets/images/balkony-page/balkony-heading/photo.jpg')
      p.balkony-heading__p &nbsp;<b>2005 </b> &nbsp;   <b> </b> ,  &nbsp;. &nbsp;  , , . ,      &nbsp; .    &nbsp;  . <b>&nbsp;     . &nbsp;      &nbsp;  &nbsp; &nbsp;   &nbsp; .</b>
</template>

<script>
  import Mixins from '../../assets/scripts/mixins'

  export default {
    mixins: [Mixins],
    props: {
      handlers: Object
    }
  }
</script>

<style lang='stylus'>
  .balkony-heading
    &__cont
      margin: 20px 0
      padding: 0 20px

    &__title
      margin: 0 0 6px
      color: #2B2A29
      font-size: 20px
      font-weight: 600

    &__subtitle
      margin: 0 0 14px
      color: #707070
      font-size: 20px
      font-weight: 400

    &__p-main
      margin: 0 0 20px
      color: #000000
      font-size: 13px

    &__p
      margin: 0
      color: #000000
      font-size: 13px

    &__img-main
      cursor: pointer

    &__img
      width: 200px
      margin: 0 10px 0 0
      float: left


  @media (max-width: 769px)
    .balkony-heading
      &__cont
        padding: 0

  @media (max-width: 481px)
    .balkony-heading
      &__img
        width: 100%
        margin: 0 0 10px
        float: none
</style>


